import React, { Component } from "react";
import { Header, Button, Menu } from "semantic-ui-react";
import CanvasDraw from "react-canvas-draw";
import axios from "axios";

import "../style/App.css";

const API = "https://icem-2020.herokuapp.com";

const colors = [
  "black",
  "grey",
  "brown",
  "pink",
  "purple",
  "violet",
  "blue",
  "teal",
  "green",
  "olive",
  "yellow",
  "orange",
  "red"
];

export default class Prepare extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: false,
      activeColor: colors[0],
      brushRadius: 5,
      lazyRadius: 0,
      activeItem: "black"
    };
  }

  handleItemClick = (e, { name }) => this.setState({ activeItem: name });

  handleClick = (e, { name }) => this.setState({ activeColor: name });

  componentDidMount() {
    document.title = "ICEM 2020 - Whiteboard";
    // check if user already logged in
    // if (localStorage.accessToken) {
    //   this.checkLogin();
    // } else {
    //   console.log("Token not found!");
    //   window.location.href = "/";
    // }
  }

  checkLogin() {
    this.setState({ loading: true });
    axios.get(API + "/users/profile").then(res => {
      // console.log(res);
      this.setState({
        username: res.data.id,
        team_name: res.data.team_name,
        university: res.data.university,
        loading: false
      });
    });
  }

  submit() {}

  render() {
    return (
      <div className="App">
        <header className="App-header">
          <div id="header">
            <Header as="h1" icon className="white-txt">
              Time Left: 90 sec
              <Header.Subheader className="white-txt">
                {this.state.university + " @ ICEM 2020"}
              </Header.Subheader>
            </Header>
          </div>

          <div className="whiteboard-menu">
            <Menu
              secondary
              vertical
              inverted
              className="color-bar"
              size="large"
            >
              <Menu.Item header></Menu.Item>

              {/* {colors.map(c => (
                <Menu.Item
                  key={c}
                  name={c}
                  active={this.state.activeColor === c}
                  color={c}
                  onClick={this.handleClick}
                />
              ))} */}

              <Menu.Item>
                <Button
                  size="big"
                  color="blue"
                  onClick={() => {
                    this.Canvas.undo();
                  }}
                >
                  Undo
                </Button>
              </Menu.Item>

              <Menu.Item>
                <Button
                  size="big"
                  color="red"
                  onClick={() => {
                    this.Canvas.clear();
                  }}
                >
                  Clear
                </Button>
              </Menu.Item>
            </Menu>
            <CanvasDraw
              ref={canvasDraw => (this.Canvas = canvasDraw)}
              className="whiteboard-size"
              lazyRadius={this.state.lazyRadius}
              brushColor={this.state.activeColor}
              brushRadius={this.state.brushRadius}
            />
          </div>
          <br />
          <div id="footer">
            <Button
              color="green"
              size="massive"
              onClick={this.Submit}
              disabled={this.state.loading}
              content={this.state.loading ? "Loading..." : "Submit"}
            />
          </div>
        </header>
      </div>
    );
  }
}
